<template>
<div class="loginCloud">
    <div class="logc" style="background:url(static/images/loginbg.png);height:100%;background-size:cover;background-size: 100% 100%;">
        <div class="loginHeader">
            <span>
          <img src="../../assets/img/posLogo.png">
        </span>
            <!--<span class='point'>·</span>
        <span class='pms'>MiHotel</span>-->
        </div>
        <div class="loginForm" @click="goAnchor" id="input-text-box">
         
            <a class="mint-cell mint-field is-nolabel up"> 
            <div class="mint-cell-left"></div> <div class="mint-cell-wrapper"><div class="mint-cell-title"> 
            <span class="mint-cell-text"></span> </div> <div class="mint-cell-value">
              <input placeholder="用户名" type="text" class="mint-field-core" v-model="formOption.user" style=" text-transform: uppercase">
              <div class="mint-field-clear" style="">
                <i class="mintui mintui-field-error"></i></div>
                 <span class="mint-field-state is-default"><i class="mintui mintui-field-default"></i></span>
                  <div class="mint-field-other"></div></div> </div> <div class="mint-cell-right"></div></a>
            <mt-field placeholder="请输入密码" type="password" v-if="formOption.password.length <32" v-model="formOption.password"></mt-field>
            <mt-field placeholder="请输入密码" type="text" v-else @input.native="formOption.password=''" v-model="vsave"></mt-field>
        </div>
        <div class="centerIn">
            <mt-button type="primary" size="large" @click="login">登录</mt-button>
        </div>
        <div class="remember">
            <mt-checklist v-model="value" :options="['记住账号密码']"></mt-checklist>
            <div class="settings" @click="gotoSetApi">
                <i data-v-06d1fadb class="iconfont icon-shezhi"></i>连接设置
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    Toast
} from "mint-ui";
import {
    rsalogin
} from "../../assets/js/RSA_Stripped.js";
import md5 from "js-md5";
export default {
    data() {
        return {
            formOption: !!localStorage.LoginOption ?
                JSON.parse(localStorage.LoginOption) : {
                    user: "",
                    password: ""
                },
            value: ["记住账号密码"],
            vsave: "保存的密码",
            rsaKey: null
        };
    },
    methods: {
        login() {
            var errorMsg = {
                user: "请填写用户名",
                password: "请填写密码"
            };
            var errors = "";
            var postForm = _.transform(this.formOption, (result, n, key) => {
                if (!n && _.isEmpty(errors)) {
                    errors = errorMsg[key];
                    return;
                } else {
                    if (key != "password") {
                        if (n) {
                            result[key] = n.toUpperCase();
                        }
                    } else {
                        if (n.length < 6) {
                            result[key] = md5(n);
                        } else {
                            result[key] = n;
                        }
                    }
                }
            });

            postForm.checkCancelFlag = 0;
            if (!_.isEmpty(postForm) && Object.keys(postForm).length == 3) {
                console.log(_.isEmpty(this.value));
                if (_.isEmpty(this.value)) {
                    localStorage.removeItem("LoginOption");
                } else {
                    // let postForms = _.cloneDeep(postForm);
                    // postForms.password = this.formOption.password;
                    localStorage.setItem("LoginOption", JSON.stringify(postForm));
                }
                this.$http
                    .post("/pos/posUnion/updateCheckUserInfo", postForm)
                    .then(res => {
                        if (res.data.errorCode == 0) {
                            //全局token
                            // localStorage.setItem("JWT_TOKEN", res.data.retVal);
                            // var redirect = this.$route.query.redirect == '/login' ? '/' : this.$route.query.redirect;
                            this.getParams();
                            let ps = !!localStorage.ps ?
                                JSON.parse(localStorage.ps) : {
                                    accountLogUser: "",
                                    invoice: false,
                                    printPayCount: 1,
                                    printRoomCount: 1
                                }
                            ps.accountLogUser = postForm.user;
                            localStorage.ps = JSON.stringify(ps);
                            localStorage.isLogin = "true";
                            this.$router.push({
                                path: "/"
                            });
                        } else {
                            Toast({
                                message: res.data.errorMsg,
                                position: "bottom",
                                duration: 3000
                            });
                        }
                    });
            } else {
                Toast({
                    message: errors,
                    position: "bottom",
                    duration: 3000
                });
            }
        },
        gotoSetApi() {
            this.$router.push("/settingApiCloudDining");
        },
        goAnchor(event) {
            var inputTextBox = document.getElementById("input-text-box");

            setInterval(function () {
                inputTextBox.scrollIntoView(true);
            }, 200);
        },
        getParams() {
            this.$http
                .post("/pos/posUnion/getParams", {}).then(res => {
                    if (res.data.errorCode == 0 && res.data.result) {
                        var cp = {};
                        res.data.result.forEach((val, key, arr) => {
                            cp[val.item] = val.setValue;
                        });
                        localStorage.cloudParams = JSON.stringify(cp);
                    }
                });
        }
    },
    mounted() {
        if (this.$route.query.jwtToken != null) {
            localStorage.setItem("JWT_TOKEN", this.$route.query.jwtToken);
            this.$router.push({
                path: "/anypay"
            });
            return;
        }
    }
};
</script>

<style lang="less">
.loginCloud {
    height: 100%;

    .logc {
        overflow: hidden;
        user-select: none;

        ::-webkit-input-placeholder {
            /* WebKit browsers */
            font-size: 0.4rem;
            color: #fff;
            text-decoration: none;
        }

        .mint-cell-wrapper {
            background: none;
            padding: 0;
            overflow: visible;
        }

        .loginHeader {
            margin: 2.2rem 0 1.5rem 1.2rem;
            color: #fff;
            width: 7.5rem;
            text-align: center;

            img {
                width: 5.6rem;
            }

            span {
                vertical-align: top;
                position: relative;
                top: 0.1rem;
            }

            .point {
                font-size: 1rem;
                top: 0.5rem;
            }

            .pms {
                font-size: 0.6rem;
                top: 0.8rem;
                left: 0.3rem;
            }

            .iconfont {
                font-size: 1.6rem;
                color: #fff;
            }
        }

        .mintui-field-error {
            font-size: 0.5rem;
            margin-right: 0.2rem;
        }

        .mint-cell-value {
            display: block;
            position: relative;
        }

        .mint-field-core {
            text-align: center;
            height: 1.2rem;
            opacity: 0.8;
            font-size: 0.4rem;
            background: none;
            width: 80%;
            margin: 0 0.73rem;
            color: #fff;
            border: none;
            text-decoration: none;
            outline: none;
        }

        .mint-field-clear {
            position: absolute;
            right: 0;
        }

        .mint-cell {
            color: #fff;
            width: 7.3rem;
            height: 1.2rem;

            margin: 0 auto;
            margin-top: 30px;
            font-size: 0.5rem;
            background: none;
        }

        .mint-cell:last-child {
            background: none;
        }

        .mint-cell-wrapper {
            border: 1px solid #f4f4f4;
            border-radius: 0.6rem;

            @media only screen and (-webkit-min-device-pixel-ratio: 2),
                only screen and (min--moz-device-pixel-ratio: 2),
                only screen and (-o-min-device-pixel-ratio: 2/1) {
                border: 2px solid #f4f4f4;
            }
        }

        .mint-cell-title {
            text-align: left;
        }

        .centerIn {
            width: 7.3rem;
            margin: 0 auto;
            margin-top: 30px;

            button {
                height: 1.2rem;
                font-size: 0.46rem;
                border-radius: 0.6rem;
                background: #f6a623;
                background-image: linear-gradient(90deg, #f6a129 0%, #ff6e61 98%);
                border-radius: 100px;
            }
        }

        .remember {
            display: flex;
            padding: 0 35px;

            .mint-cell-wrapper {
                border: none;
                border-radius: 0;
                background: none;
            }

            .mint-cell {
                background: none;
                border: none;
                width: 191px;
            }

            .mint-checkbox-core {
                border-radius: 0.06rem;
                width: 0.5rem;
                height: 0.5rem;
                margin-right: 0.1rem;
            }

            .mint-checkbox-core::after {
                width: 0.15rem;
                height: 0.15rem;
                top: 0.06rem;
                left: 0.15rem;
                transform: rotate(45deg) scale(1, 2.2) !important;
                border-width: 1px !important;

                @media only screen and (-webkit-min-device-pixel-ratio: 2),
                    only screen and (min--moz-device-pixel-ratio: 2),
                    only screen and (-o-min-device-pixel-ratio: 2/1) {
                    border-width: 2px !important;
                }
            }

            .mint-checkbox-label {
                font-size: 0.4rem;
            }

            .mint-checkbox-input:checked+.mint-checkbox-core {
                border-color: #f89635;
                background: #fff;
            }

            .mint-checkbox-input:checked+.mint-checkbox-core::after {
                border-color: #f89635;
            }
        }

        .settings {
            display: inline-block;
            font-size: 0.373rem;
            color: #ffffff;
            text-align: right;
            margin-top: 42px;

            .iconfont {
                margin-right: 5px;
            }
        }
    }

    .mint-toast {
        border-radius: 0.2rem;

        .iconfont {
            font-size: 2.5rem;
        }
    }
}
</style>



